      enTitle: 'Your Data Protection Rights under the California Consumer Privacy Act (CCPA)',
      enBody: <>
      <p __cpp="1">If you are a California resident, you are entitled to learn what data we collect about you, ask to
      delete your data and not to sell (share) it. To exercise your data protection rights, you can make certain
      requests and ask us:</p>
   <p __cpp="1">11.1. What personal information we have about you. If you make this request, we will return to you:</p>
   <p __cpp="1">11.0.1. The categories of personal information we have collected about you.</p>
   <p __cpp="1">11.0.2. The categories of sources from which we collect your personal information.</p>
   <p __cpp="1">11.0.3. The business or commercial purpose for collecting or selling your personal information.</p>
   <p __cpp="1">11.0.4. The categories of third parties with whom we share personal information.</p>
   <p __cpp="1">11.0.5. The specific pieces of personal information we have collected about you.</p>
   <p __cpp="1">11.0.6. A list of categories of personal information that we have sold, along with the category of any
      other company we sold it to. If we have not sold your personal information, we will inform you of that fact.</p>
   <p __cpp="1">11.0.7. A list of categories of personal information that we have disclosed for a business purpose,
      along with the category of any other company we shared it with.</p>
   <p __cpp="1">Please note, you are entitled to ask us to provide you with this information up to two times in a
      rolling twelve-month period. When you make this request, the information provided may be limited to the personal
      information we collected about you in the previous 12 months.</p>
   <p __cpp="1">11.2. To delete your personal information. If you make this request, we will delete the personal
      information we hold about you as of the date of your request from our records and direct any service providers to
      do the same. In some cases, deletion may be accomplished through de-identification of the information. If you
      choose to delete your personal information, you may not be able to use certain functions that require your
      personal information to operate.</p>
   <p __cpp="1">11.3. To stop selling your personal information. We dont sell or rent your personal information to any
      third parties for any purpose. We do not sell your personal information for monetary consideration. However, under
      some circumstances, a transfer of personal information to a third party, or within our family of companies,
      without monetary consideration may be considered a sale under California law. You are the only owner of your
      Personal Data and can request disclosure or deletion at any time.</p>
   <p __cpp="1">If you submit a request to stop selling your personal information, we will stop making such transfers.
   </p>
   <p __cpp="1">Please note, if you ask us to delete or stop selling your data, it may impact your experience with us,
      and you may not be able to participate in certain programs or membership services which require the usage of your
      personal information to function. But in no circumstances, we will discriminate against you for exercising your
      rights.</p>
   <p __cpp="1">To exercise your California data protection rights described above, please send your request(s) by
      email: admin@intenseproxy.com.</p>
   <p __cpp="1">Your data protection rights, described above, are covered by the CCPA, short for the California Consumer
      Privacy Act. To find out more, visit the official California Legislative Information website. The CCPA took effect
      on 01/01/2020.</p>
      </>
